/* ==========================================================================
   WooCommerce Cart page styles - begin
   ========================================================================== */

.mkdf-woocommerce-page {
	
	table.cart {
		
		thead {
			
			tr {
				border: 0;
				border-bottom: 1px solid $default-border-color;
				
				th {
					padding: 10px 0;
					color: $default-heading-color;
					text-align: left;
					@include mkdfH5();
				}
			}
		}
		
		tbody {
			
			tr {
				border: 0;
			}
		}
		
		tr.cart_item {
			border-bottom: 1px solid $default-border-color;
			
			td {
				padding: 25px 0;
				vertical-align: middle;
				text-align: inherit;
				
				&.product-remove {
					padding-left: 0;
					padding-right: 0;
					
					a {
						color: $default-text-color;
						font-size: 0;
						line-height: 1;
						
						&:after {
							content: "\f404";
							display: inline-block;
							vertical-align: middle;
							speak: none;
							color: inherit;
							font-family: "Ionicons";
							font-size: 25px;
							line-height: inherit;
							font-style: normal;
							font-weight: normal;
							font-variant: normal;
							text-transform: none;
							text-rendering: auto;
							-webkit-font-smoothing: antialiased;
							-moz-osx-font-smoothing: grayscale;
						}
						
						&:hover {
							color: $default-heading-color;
						}
					}
				}
				
				&.product-thumbnail {
					width: 86px;
					padding-right: 10px;
					
					a, img {
						display: block;
						width: 100%;
					}
				}
				
				.variation {
					
					> * {
						display: inline-block;
						vertical-align: middle;
						padding: 0;
						margin: 0;
					}
					
					p {
						margin: 0;
					}
				}
			}
		}
		
		td.actions {
            padding: 45px 0 56px;
			text-align: inherit;
			
			.coupon {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				float: left;
				
				label {
					display: none;
				}
                input{
                    @include mkdfPlaceholder{
                        text-transform: none;
                        font-size: 20px;
                    }
                }
			}
			
			> input {
				float: right;
			}

			>.button{

                @include ipad-landscape{
                    margin-top: 0;
                }
			}

			.button {
				margin: 0 0 0 22px;
				
				@include phone-portrait{
					margin: 10px 0 0;
				}
			}
			.button[name="update_cart"]{
				float: right;
				margin-top: 16px;

				@include ipad-portrait{
					margin-top: 0;
				}

				@include phone-portrait{
					float: none;
				}
			}
		}
	}
	
	.cart-collaterals {
		@include mkdfRelativeHolderLayout;
		margin: 0 0 50px;
		
		h2 {
			position: relative;
			display: block;
			margin: 19px 0 26px;
		}
		
		table {

			.cart-subtotal,
			.woocommerce-shipping-totals,
			.order-total{
				th{
					color: $default-heading-color;
				}
			}
			margin: 0 0 35px;
			
			tr {
				border: 0;
				border-bottom: 1px solid $default-border-color;
			}
			
			td, th {
				padding: 14px 0;
				text-align: left;
				vertical-align: middle;
			}
			
			th {
				width: 20%;
				padding-right: 5px;
				border: 0;
				@include mkdfH5();

				@include ipad-landscape{
					padding-right: 25px;
				}
			}
		}
		
		tr.shipping {
			
			#shipping_method {
				list-style: none;
				margin: 0 0 7px;
				
				li {
					margin: 3px 0;
					padding: 0;
					
					> * {
						display: inline-block;
						vertical-align: middle;
                        margin: 0 8px 0 0;
					}
				}
			}
		}
		
		.woocommerce-shipping-calculator {
			
			.shipping-calculator-button {
				display: block;
				margin: 0 0 10px;
			}
			
			p {
				margin: 0;
			}
		}
	}
	
	.cross-sells {
		margin: 30px 0 0;
		border-top: 1px solid $default-border-color;
		
		> h2 {
			display: block;
			margin: 36px 0 50px;
			text-align: center;
		}
	}
	
	.cart_totals {
		margin: 40px 0 20px;

        h2{
            @include mkdfH3();
        }
	}
}

.mkdf-woocommerce-page {
	
	.cart-empty {
		position: relative;
		display: block;
		@include mkdfH3();
		text-align: center;
		color: $default-heading-color;
		margin: 40px 0 33px;
	}
	
	.return-to-shop {
		position: relative;
		display: block;
		text-align: center;
		margin: 0 0 60px;
	}
}
/* ==========================================================================
   WooCommerce Cart page styles - end
   ========================================================================== */