/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.mkdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: top;
    height: 100%;
    margin: 0;
    padding: 0;
    transition: all 0.3s ease;
    cursor: pointer;

    .mkdf-sc-opener-count{
        background-color: $first-main-color;
        border-radius: 50%;
        padding: 3px 5px 1px 5px;
        font-size: 9px;
        line-height: 1.2em;
        color: #fff;
        font-weight: 600;
        vertical-align: middle;
        display: block;
        position: absolute;
        top: 26%;
        left: 65%;
    }

    .touchevents & {
        cursor: pointer;
    }
    
    &:hover {

        .mkdf-sc-opener-count{
            background-color: #fff;
            color: #000;
            transition: all 0.3s ease;
        }

        background-color: $first-main-color;
        
        .mkdf-header-cart{
            color: #fff;
        }
        
        
        .mkdf-sc-dropdown {
            max-height: 100vh;
            pointer-events: auto;
            color: #000;
            border-bottom: 1px solid #f8f8f8;

            .mkdf-sc-dropdown-inner {
                opacity: 1;
                transition: .7s .1s;
            }
        }
        svg{
            path{
                fill: #fff;
            }
        }
    }

    .mkdf-shopping-cart-inner {
        @include mkdfRelativeHolderLayout();
        height: 100%;
        padding: 0 27px;
        box-sizing: border-box;

        .mkdf-empty-cart{
            font-weight: 500;
            width: 100%;
            box-sizing: border-box;
            padding: 3% 0 3% 28px;
            height: 100%;
            background-color: transparent;
            background-image: url(../../assets/img/default_pattern.png);
        }
    }

    .mkdf-header-cart {
        position: relative;
        display: flex;
        align-items: center;
        height: 100%;
        transition: all 0.3s ease;
        
        &:hover{
            color: #fff;
        }

        .mkdf-sc-opener-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
            line-height: 1;
        }

        &.mkdf-header-cart-icon-pack {
            
            .mkdf-sc-opener-icon {
                font-size: 18px;

                svg{
                    display: block;
                    width: 18px;
                }

                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.mkdf-header-cart-svg-path {
            
            .mkdf-sc-opener-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                }
            }
        }
    }
}

.mkdf-dark-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.mkdf-light-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-light-color;

    svg{
        .st0{
            fill: $header-light-color;
        }
    }

    &:hover {
        color: #fff;
    }
}

.mkdf-header-tabbed {
    .mkdf-menu-area{
        .mkdf-sc-dropdown {
            top: calc(100% + 1px);
            border-top: none;
            .mkdf-sc-dropdown-inner{
                border-top: none;
            }
        }
    }
}

.mkdf-sc-dropdown {
    position: absolute;
    top: 100%;
    left: auto;
    right: -2px;
    text-align: left;
    box-sizing: border-box;
    border-left: 1px solid #f8f8f8;
    border-right: 1px solid #f8f8f8;
    background-color: #fff;
    max-height: 0;
    pointer-events: none;
    transition: all .7s, border 0s;
    
    .mkdf-sc-dropdown-inner {
        opacity: 0;
        @include mkdfRelativeHolderLayout();
        width: 375px;
        padding: 24px 30px;
        box-sizing: border-box;
        transition: .2s;

        &.mkdf-empty-sc{
            padding: 0;
        }
    }
    
    .mkdf-sc-dropdown-items {
        @include mkdfRelativeHolderLayout();
        margin-top: 6px;
    }
    
    .mkdf-sc-dropdown-item {
        position: relative;
        display: flex;
        align-items: flex-start;
        margin-top: 22px;
        
        &:first-child {
            margin-top: 0;
        }
    }
    
    .mkdf-sc-dropdown-item-image {
        flex-shrink: 0;
        width: 80px;
        margin-right: 20px;
        
        a, img {
            display: block;
        }
    }
    
    .mkdf-sc-dropdown-item-content {
        @include mkdfRelativeHolderLayout();
        padding-right: 20px;
    }
    
    .mkdf-sc-dropdown-item-title {
        margin: 0;
        font-size: 18px;
        line-height: 1.4em;
    }
    
    .mkdf-sc-dropdown-item-price {
        margin: 0;
        @include mkdfH5();
        font-size: 16px;
        font-weight: 400;
    }
    
    .mkdf-sc-dropdown-item-quantity {
        margin: 0;
        @include mkdfH6();
        font-size: 14px;
        line-height: 1.7em;
    }
    
    .mkdf-sc-dropdown-item-remove {
        position: absolute;
        top: -2px;
        right: 0;
        font-size: 20px;
        line-height: 1;
    }
    
    .mkdf-sc-dropdown-subtotal {
        position: absolute;
        bottom: 109px;
        width: 100%;
        left: 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 30px;
        background-image: url('../../assets/img/default_pattern.png');
        border: 1px solid #e8e8e9;
        border-left: none;
        border-right: none;
        padding: 15px 27px 15px;
        box-sizing: border-box;

        .mkdf-sc-dropdown-total,
        .mkdf-sc-dropdown-total-amount{
            @include mkdfH5();
            line-height: 26px;
            font-size: 18px;
            text-transform: none;
        }

        .mkdf-sc-dropdown-total,
        .woocommerce-Price-amount{
            letter-spacing: 0;
            color: $default-heading-color;
        }

        
        > * {
            margin: 0;
        }
    }
    
    .mkdf-sc-dropdown-button-holder {
        position: relative;
        display: block;
        width: 224px;
        vertical-align: middle;
        margin: 120px auto 0;
        padding-bottom: 6px;
    }
    
    .mkdf-sc-dropdown-button {
        @include mkdfButtonDefaultStyle();
        @include mkdfButtonSize(huge);
        @include mkdfButtonSolidColor();
        line-height: 1.3em;
        
        &:hover {
            @include mkdfButtonSolidHoverColor();
        }
    }
}

.mkdf-vertical-menu-area {

    .mkdf-sc-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */