/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.mkdf-woo-single-page {
	
	&.mkdf-woo-single-thumb-on-left-side {
		
		.mkdf-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {

					.woocommerce-product-gallery__wrapper {
						width: 100%;
						margin: 0;
						overflow: hidden;
					}
					
					$thumb_size: 100;
					$space_between_items: 20;
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							$first_thumb_offset: $thumb_size + $space_between_items;
							
							left: #{$first_thumb_offset}px;
							width: calc(100% - #{$first_thumb_offset}px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: #{$thumb_size}px;
								padding: 0 0 #{$space_between_items}px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
						}
					}
					
					$thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
					@for $i from 0 to length($thumb_3_columns_label) {
						&.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
							$column_value: $i+2;
							$column_size: 100 / $column_value;
							$column_width: calc(#{100% / $column_value} + #{$space_between_items / $column_value}px);
							
							.woocommerce-product-gallery__image {
								
								&:not(:first-child) {
									height: $column_width;
								}
								
								$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
								@for $i from 0 to length($thumbnail_numbers) {
									$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
									
									&:nth-child(#{$thumbnail_number_value}#{n}) {
										$first_param: ($column_size * $i)#{'%'};
										$second_param: ($space_between_items / $column_value * $i)#{px};
										$topPos: calc(#{$first_param} + #{$second_param});
										
										top: $topPos;
									}
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include mkdfRelativeHolderLayout();
	    float: none;
	    margin: 0;
    }
        
    .mkdf-single-product-content {
        @include mkdfRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 51%;
		    margin: 0;
		    padding: 0 29px 0 0;
		    float: left;
		    box-sizing: border-box;

		    .woocommerce-product-gallery__wrapper {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include mkdfRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .mkdf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 45%;
        padding: 0 0 0 10px;
        float: left;
        box-sizing: border-box;
	
	    .mkdf-single-product-title {
		    display: block;
			margin: 14px 0 18px;
	    }

		.in-stock,
        .woocommerce-review-link{
			display: none;
		}

	    .price {
		    @include mkdfWooCommercePriceStyle();
            margin: 4px 0 0;
			font-weight: 400;
	    }
	    .mkdf-ps-quick-info{
            margin: 51px 0 0;
        }
	    .woocommerce-product-rating {
		    @include mkdfRelativeHolderLayout();
		    line-height: 1;
			margin: 63px 0 1px;

			@include laptop-landscape-mac{
				margin: 5px 0 1px;
			}

            .star-rating{
                float: left;
                color: $first-main-color;
                font-size: 12px;
                vertical-align: top;
            }
	    }
	
	    .woocommerce-product-details__short-description {
		    @include mkdfRelativeHolderLayout();
		    margin: 45px 0 0;
		
		    p {
			    margin: 0;
		    }
	    }
	
	    .product_meta {
		    margin: 16px 0 0;
		
		    > span {
			    display: block;
			    margin: 3px 0;
			    @include mkdfH6();
                line-height: 1.9em;
                font-weight: 500;
				text-transform: uppercase;
			    a {
                    @include mkdfH6();
				    color: $default-text-color;
                    line-height: 1.9em;
                    font-weight: 500;
					text-transform: uppercase;

				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	
	    .mkdf-woo-social-share-holder {
		    @include mkdfRelativeHolderLayout();
		    margin: 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include mkdfRelativeHolderLayout();
		    margin: 51px 0 0;
	    }
	
	    .mkdf-quantity-buttons {
		    margin: 0 11px 0 0;
	    }
    }
	
	.woocommerce-tabs {
		@include mkdfRelativeHolderLayout();
		margin: 58px 0 0;
		
		ul.tabs {
			@include mkdfRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			border-top: transparent;
			border-bottom: transparent;
			text-align: left;
			clear: both;
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;

                &:not(.active){

                    a {
                        color: #d6d6d7;
                        &:hover{
                            color: $first-main-color;
                        }
                    }
                }
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
                    padding: 13px 54px 13px 0;
                    @include mkdfH4();
				}
			}
		}
		
		.entry-content {
			@include mkdfRelativeHolderLayout();
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 9px 0 20px;
                font-size: 18px;
			}
		}
		
		table {
			margin: 12px 0 20px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 1px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-heading-color;
				width: 1%;
				padding: 0 10px 0 0;
                @include mkdfH6();
                font-size: 14px;
                font-weight: 500;
				line-height: 1.9em;
				text-transform: uppercase;
			}
			
			td {
				width: 99%;
                @include mkdfH6();
                font-size: 14px;
                font-weight: 500;
			}
		}
		
		#reviews {
			@include mkdfRelativeHolderLayout();
			margin: 4px 0 20px;
			
			h2 {
				font-size: 18px;
				line-height: 24px;
				margin: 8px 0 24px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
                    margin: 0 0 70px;
					
					&:last-child {
						margin: 0;
					}
				}

                .woocommerce-review__published-date{
                    font-size: 16px
                }

                .woocommerce-review__author{
                    @include mkdfH4();
					color: $default-heading-color;
                }

				.comment_container {
					@include mkdfRelativeHolderLayout();
					
					img {
						float: left;
						width: 70px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 100px;
					
					p {
                        margin: 7px 0 13px 0;
                        font-size: 18px;
					}
				}
			}
			
			.comment-respond {
				margin-top: 15px;
				
				.comment-reply-title {
					margin: 0 0 10px;
                    font-size: 26px;
                    font-weight: 400;
				}
				
				p {
					margin: 0;
				}
				label{
					color: #2b3651!important;
					@include mkdfH6();
					line-height: 1.3em;

				}
				.stars {
					overflow: hidden;
					
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include mkdfWooCommerceSingleProductRatingsStyle();
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 46px 0 0;
		border-top: transparent;
		
		> h2 {
            @include mkdfH3();
			display: block;
			margin: 28px 0 37px;
		}
		
		.product {
			
			.mkdf-pl-text-wrapper {
				margin: 23px 0 27px;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */