/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

@mixin qodefWooSliderHandle() {
    width: 11px;
    height: 11px;
    border-radius: 50%;
    background-color: $first-main-color;
    border: none;
    position: relative;
    top: 1px;
}

.widget.woocommerce {
    $thumb: 80px;
    $inner-padding: 15px; // distance between image and content
    $widget-list-item-padding: 15px; // same as sidebar vars

    .product-categories{

        .children{
            border-top: none;
        }
        li{
            a{
                font-size: 14px;
                letter-spacing: .1em;
                text-transform: uppercase;
                line-height: 26px;
                font-weight: 500;
            }
        }
    }

    &.widget_product_tag_cloud{
        .mkdf-widget-title-holder{
            margin-bottom: 11px;
        }
    }

    clear: both;

    li {
        position: relative;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // title

    .product-title {
        @include mkdfH5();
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // star rating

    li:not(.wc-layered-nav-rating) {
        .star-rating {
            margin-bottom: 7px;
            margin-top: 7px;

            span {
                max-width: 100px;
            }
        }
    }

    li.wc-layered-nav-rating {
        a {
            text-decoration: none;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price

    .amount {
        color: inherit;

        span { // currency symbol
            color: inherit;
        }
    }

    del,
    ins {
        display: block;

        // currency symbol
        span {
            color: inherit;
        }
    }

    ins {
        text-decoration: none;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // layered nav

    &.widget_layered_nav {
        .chosen {
            a {
                color: $first-main-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price filter

    &.widget_price_filter {
        form {
            margin-top: 32px;
            margin-bottom: 6px;
        }
        .mkdf-widget-title{
            margin-top: 4px;
        }
        .price_slider_wrapper {
            .ui-widget-content {
                background-color: #c8c8c8;
                height: 2px;
                position: relative;

                .ui-slider-handle {
                    background-color: $first-main-color;
                    border-radius: 50%;
                    cursor: pointer;
                    height: 11px;
                    margin: 0 0 0 -1px;
                    outline: medium none;
                    position: absolute;
                    top: -5px;
                    width: 11px;
                    z-index: 2;

                    &:last-child {
                        transform: translateX(-50%);
                    }
                }

                .ui-slider-range {
                    background-color: $first-main-color;
                    display: block;
                    height: 2px;
                    position: absolute;
                    top: 0;
                    z-index: 1;
                }
            }
        }

        .price_slider_amount {
            margin: 40px 0 0;

            .button {
                float: left;
                padding: 0 34px;
                line-height: 46px;

                @include mkdfButtonOutlineColor();
                background-color: $first-main-color;
                color: #fff;

                &:hover {
                    @include mkdfButtonOutlineHoverColor();
                    box-shadow: $first-hover-box-shadow;
                    transition: box-shadow $first-main-transition;
                }
            }

            .price_label {
                float: right;
                @include mkdfH6();
                font-size: 12px;
                line-height: 46px;
                font-weight: 500;
                text-transform: uppercase;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images

    &.widget_shopping_cart,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
        ul {
            li {
                min-height: $thumb;
                position: relative;

                a {
                    img {
                        left: 0;
                        max-width: $thumb;
                        position: absolute;
                        top: $widget-list-item-padding;
                        width: 100%;
                    }

                    span {
                        box-sizing: border-box;
                        display: block;
                        padding: 0 0 0 #{$thumb + $inner-padding};
                        position: relative;
                    }
                }

                > *:not(a) {
                    clear: both;
                    float: left;
                    margin: 0 0 0 #{$thumb + $inner-padding};
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images and plain text next to image

    &.widget_shopping_cart {
        a {
            display: block;
            padding: 0 0 0 #{$thumb + $inner-padding};

            @include mkdfH5();
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // recent reviews

    //&.widget_recent_reviews {
    //    .reviewer {
    //    }
    //}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // shopping cart

    &.widget_shopping_cart {
        .remove {
            font-size: 0;
            font-weight: normal;
            line-height: 1;
            position: absolute;
            right: 0;
            top: 5px;
            text-decoration: none;

            &:after {
                content: "\4d";
                display: block;
                font-family: 'ElegantIcons';
                font-size: 15px;
                line-height: 15px;
                position: relative;
            }
        }

        .variation {
            @include mkdfRelativeHolderLayout();

            > * {
                display: inline-block;
                margin: 0;
                vertical-align: middle;

                p {
                    margin: 0;
                }
            }
        }

        .total {
            margin: 17px 0 13px;
        }

        .buttons {
            .button {
                @include mkdfButtonDefaultStyle();
                @include mkdfButtonSize();
                @include mkdfButtonSolidColor();

                &:hover {
                    @include mkdfButtonSolidHoverColor();
                }

                &:first-child {
                    float: left;
                }

                &:last-child {
                    float: right;
                }
            }
        }
    }
}

.widget {
    .wp-block-woocommerce-filter-wrapper {
        .wc-block-components-price-slider__range-input-wrapper{
            &:before {
                background-color: #c8c8c8;
                height: 2px;
                opacity: 1;
            }
        }
        
        .wc-block-components-price-slider__range-input-progress {
            height: 2px;
            --range-color: #ff5e14;
        }
        
        .wc-block-components-price-slider__range-input {
            &::-webkit-slider-thumb {
                @include qodefWooSliderHandle();
            }
    
            &::-moz-range-thumb {
                @include qodefWooSliderHandle();
            }
    
            &::-ms-thumb {
                @include qodefWooSliderHandle();
            }
        }
    
        .wc-block-components-price-slider__actions {
            justify-content: space-between;
            
            button[type=submit]:not(.wp-block-search__button).wc-block-components-filter-submit-button {
                font-size: 16px;
                margin-left: auto;
            }
        }
        
        .wc-block-components-price-slider__range-text {
            @include mkdfH6();
            font-size: 12px;
            line-height: 46px;
            font-weight: 500;
            text-transform: uppercase;
            justify-content: flex-end;
            margin: 19px 0 20px;
            
            span {
                font-size: inherit;
                
                &:first-child {
                    &:after {
                        content: '—';
                        margin: 0 5px;
                    }
                }
            }
        }
        
        .wc-block-components-filter-reset-button {
            @include mkdfButtonDefaultStyle();
            @include mkdfButtonSolidColor();
            @include mkdfButtonSize();
            text-decoration: none;
            
            &:hover {
                @include mkdfButtonSolidHoverColor();
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */