/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 0;
    width: auto;
    height: 24px;
    margin: 0;
    padding: 0 16px;
    color: #fff;
    font-size: 8px;
    line-height: 24px;
    letter-spacing: 1px;
    text-align: center;
    z-index: 100;
    box-sizing: border-box;
    transform: translateZ(0);
}

@mixin mkdfWooCommerceSaleStyle() {
    left: 0;
    background-color: rgba($default-heading-color, .5);
}

@mixin mkdfWooCommerceOutOfStockStyle() {
    right: 0;
    background-color: rgba($default-heading-color, .5);
}

@mixin mkdfWooCommerceNewProductStyle() {
    left: 27%;
    background-color: rgba($default-heading-color, .5);
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommercePriceStyle() {
    position: relative;
    display: block;
    margin: 14px 0 0;
    line-height: 1;
    color: $default-heading-color;
    
    * {
        font-size: inherit;
        line-height: inherit;
    }
    
    del {
        margin: 0 8px 0 0;
        color: rgba($default-heading-color, .8);
    }
    
    ins {
        text-decoration: none;
    }
}

@mixin mkdfWooCommerceProductLabelStyle(){
    @include mkdfH6();
    letter-spacing: 0.02em;
    line-height: 0.2em;
    font-size: 14px;
    margin: 20px 15px 0 20px;
    padding: 27px 13px;
    display: inline-block;
    width: auto;
    min-width: 56px;
    border-radius: 2px;
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "ElegantIcons";
    font-size: 12px;
    line-height: 1;
    letter-spacing: 2px;
    color: $default-text-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin mkdfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: top;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\e031\e031\e031\e031\e031";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            content: "\e033\e033\e033\e033\e033";
        }
    }
}

@mixin mkdfWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        content: "\e031";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
        color: $first-main-color;
    }
    
    &.active:after {
        content: "\e033\e033\e033\e033\e033";
        position: absolute;
        right: -4.5px;
        bottom: 7.5px;
        letter-spacing: 6.5px;
        display: block;
        @extend %star-ratings;
        color: $first-main-color;
        z-index: -1;
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */