/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .mkdf-content,
div.woocommerce {

    .mkdf-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        .mkdf-quantity-input,
        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-text-color;
            background-color: $default-border-color;
            border: 1px solid $default-border-color;
            text-align: center;
            -webkit-user-select: none;
            box-sizing: border-box;
        }

        .mkdf-quantity-input {
            position: relative;
            height: 48px;
            width: 50px;
            padding: 0;
            margin: 0 22px 0 0;
            font-family: inherit;
            line-height: 42px;
            outline: 0;
            border-radius: 2px;
            font-size: 16px;
        }

        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            position: absolute;
            right: 0;
            width: 22px;
            height: 25px;
            line-height: 25px;
            border-left: 0;
            cursor: pointer;
            transition: color .15s ease-in-out;
            background-color: $first-main-color;
            color: #fff;
            border-color: $first-main-color;

            &:hover {
                color: #fff;
                background-color: $second-main-color;
                border-color: $second-main-color;
            }
        }

        .mkdf-quantity-plus {
            top: 0;
            border-bottom: 0;
            border-top-left-radius: 2px;
            border-top-right-radius: 2px;

            &.icon_plus{
                &:before{
                    content: "\32";
                }
            }
        }

        .mkdf-quantity-minus {
            bottom: 0;
            border-bottom-left-radius: 2px;
            border-bottom-right-radius: 2px;

            &.icon_minus-06{
                &:before{
                    content: "\33";
                }
            }
        }
    }
}

.woocommerce-page .mkdf-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
        padding: 18px 16px 12px;
        @include mkdfH4();
    }

    #coupon_code{
        font-size: 18px;
        line-height:  26px;
        letter-spacing: 0;
        font-weight: 300;
        padding: 18px 16px 2px;

        @include mkdfPlaceholder{
            font-size: 18px;
            line-height:  26px;
            letter-spacing: 0;
            font-weight: 300;
        }
    }

    .coupon {

        input[type="text"] {
            width: 200px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */