/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - begin
   ========================================================================== */

.woocommerce {
    
    .mkdf-onsale,
    .mkdf-out-of-stock,
    .mkdf-new-product {
        @include mkdfWooCommerceMarksGeneralStyle();
    }
    
    .mkdf-onsale {
        @include mkdfWooCommerceProductLabelStyle();
        background-color: $fourth-blue-color;
        padding: 31px 11px;
    }
    
    .mkdf-out-of-stock {
        @include mkdfWooCommerceProductLabelStyle();
        background-color: $first-main-color;
        text-transform: uppercase;
    }

    .mkdf-new-product {
        @include mkdfWooCommerceProductLabelStyle();
        background-color: $first-main-color;
        text-transform: uppercase;
    }
}

.mkdf-woo-single-page {
    
    .mkdf-single-product-content {

        .mkdf-out-of-stock,
        .mkdf-onsale {
            left: 15px;
            width: 56px;
            padding: 27px 11px;
            text-transform: uppercase;
        }

        .mkdf-new-product {
            left: 81px;
            width: 56px;
        }
    }
    
    &.mkdf-woo-single-thumb-on-left-side {
        
        .mkdf-single-product-content {
            
            .mkdf-onsale {
                left: 120px;
            }
            
            .mkdf-out-of-stock,
            .mkdf-new-product {
                right: 0;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - end
   ========================================================================== */