/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.mkdf-pl-holder {
    @include mkdfRelativeHolderLayout();
	
	&.mkdf-info-on-image {
		
		.mkdf-pli {
			
			.mkdf-pli-add-to-cart {
				margin: 25px 0 0;
			}

			.mkdf-pli-category {
				float: none;
			}
		}
	}

	&.mkdf-info-below-image{
		.mkdf-pli{
			.mkdf-pli-rating-holder{
				float: right;
			}
		}
	}

    .mkdf-pli {
        padding: 0;
        z-index: 100;

	    .mkdf-pli-title {
		    margin: 0 0 4px;
	    }
	
	    .mkdf-pli-category {
		    margin: 0;
			float:left;
			@include mkdfH6();
			font-size: 12px;
			line-height: 2.5em;
			text-transform: uppercase;

			a{
				color: $default-text-color;
			}
	    }

        .mkdf-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 0;
			@include mkdfH6();
			font-size: 12px;
        }

	    .mkdf-pli-rating-holder {
		    @include mkdfRelativeHolderLayout();
		    line-height: 1;
			width:initial;
		    margin: 3px 0 0;
			float:initial;
	    }

	    .mkdf-pli-rating {
		    @include mkdfWooCommerceRatingsStyle();
			width: initial;
			float: right;
			line-height: 2em;
			color: $first-main-color;
	    }
		.mkdf-pli-info-top{
			margin-bottom: 0;
		}
        .mkdf-pli-price {
			@include mkdfH6();
            margin: 3px 0 0;

			ins{
				text-decoration: none;
			}
        }
    }

    .mkdf-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        &:hover {

            .mkdf-pli-text {
                opacity: 1;
            }
        }

        .touchevents & {
            cursor: pointer;
        }

        .mkdf-pli-image {
            @include mkdfRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }

            .mkdf-pli-onsale,
            .mkdf-pli-out-of-stock,
            .mkdf-pli-new-product {
	            @include mkdfWooCommerceMarksGeneralStyle();
				text-transform: uppercase;
            }

            .mkdf-pli-onsale {
                @include mkdfWooCommerceProductLabelStyle();
                background-color: $fourth-blue-color;
                padding: 27px 11px;
                left:0;
            }

			.mkdf-pli-onsale{
				&+.mkdf-pli-new-product{
					left: 65px;

                    @include ipad-landscape{
                        left: 65px;
                    }
                    @include ipad-portrait{
                        left: 65px;
                    }
				}
			}

            .mkdf-pli-out-of-stock {
                @include mkdfWooCommerceProductLabelStyle();
                background-color: $first-main-color;
            }
	
	        .mkdf-pli-new-product {
                @include mkdfWooCommerceProductLabelStyle();
                background-color: $first-main-color;
				left: 0;
	        }
        }
            
        .mkdf-pli-text {
            @include mkdfAbsoluteHolderLayout();
            padding: 0;
            opacity: 0;
            box-sizing: border-box;
            transition: opacity .2s ease-out;
        }

        .mkdf-pli-text-outer {
	        @include mkdfTableLayout();
	        text-align: center;
        }

        .mkdf-pli-text-inner {
            @include mkdfTableCellLayout();
			vertical-align: bottom;

            .mkdf-pli-title,
            .mkdf-pli-category a,
            .mkdf-pli-excerpt,
            .mkdf-pli-rating,
            .mkdf-pli-price {
                color: #fff;
            }
	
	        .mkdf-pli-category a:hover {
		        color: rgba(#fff, .8);
	        }

	        .mkdf-pli-add-to-cart {

		        &.mkdf-default-skin {

			        .button,
			        .added_to_cart {
				        @include mkdfButtonSolidColor();

				        &:hover {
				            @include mkdfButtonSolidHoverColor();
				        }
			        }
		        }

		        &.mkdf-light-skin {

			        .button,
			        .added_to_cart {
				        color: $default-heading-color;
				        background-color: #fff;

				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
		
		        &.mkdf-dark-skin {
			
			        .button,
			        .added_to_cart {
				        color: #fff;
				        background-color: $default-heading-color;
				
				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
	        }

            .button,
            .added_to_cart {
	            @include mkdfButtonDefaultStyle();
				padding: 7px 24px;
	            white-space: nowrap;
	            z-index: 3;
				width: 100%;
				bottom: -1px;
            }

            .button.added {
                display: none;
            }
        }

	    .mkdf-pli-link {
		    @include mkdfAbsoluteHolderLayout();
	    }
    }

    .mkdf-pli-text-wrapper {
        @include mkdfRelativeHolderLayout();
		margin: 21px 0 27px;
    }
	
	&.mkdf-product-info-light {
		
		.mkdf-pli-inner {
			
			.mkdf-pli-text-inner {
				
				.mkdf-pli-title,
				.mkdf-pli-category a,
				.mkdf-pli-excerpt,
				.mkdf-pli-rating,
				.mkdf-pli-price {
					color: #fff;
				}
				
				.mkdf-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.mkdf-product-info-dark {
		
		.mkdf-pli-inner {
			
			.mkdf-pli-text-inner {
				
				.mkdf-pli-title,
				.mkdf-pli-category a,
				.mkdf-pli-excerpt,
				.mkdf-pli-rating,
				.mkdf-pli-price {
					color: $default-heading-color;
				}
				
				.mkdf-pli-category a:hover {
					color: rgba($default-heading-color, .8);
				}
			}
		}
	}
	
	/*************** Masonry Layout - begin ***************/
	
	&.mkdf-masonry-layout {
		
		.mkdf-pli-inner {
			width: 100%;
			height: 100%;
			
			.mkdf-pli-image {
				height: 100%;
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */