/* ==========================================================================
   WooCommerce Result and Ordering styles - begin
   ========================================================================== */
.mkdf-woocommerce-page {

    .woocommerce-result-count,
    .woocommerce-ordering {
        display: inline;
        width: 50%;
        margin: 0 0 42px;
        float: left;
    }
    .woocommerce-result-count{
        margin: 14px 0 0;
    }
    .woocommerce-result-count {
        line-height: $sselect-input-height;
        @include mkdfH6();
        font-size: 12px;
        font-weight: 500;
        text-transform: uppercase;
    }

    .woocommerce-ordering {
        text-align: right;

        .orderby {
            width: auto;
            min-width: 250px;
            margin: 0;
            text-align: initial;
        }

        .select2 {
            margin-bottom: 0;
        }
    }
}

/* ==========================================================================
   WooCommerce Result and Ordering styles - end
   ========================================================================== */